import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniLoadMore	
	},
	data() {
		return {
			headerPosition:"fixed",
			loadingType: 'more', //加载更多状态
			goodsList: [],
			mgoodsList:[],
			pn:0,
			count:1
		};
	},
	onShow() {
		if(uni.getStorageSync('cbe_token')){
			this.loadData('refresh');
		}else{
			uni.showToast({ title:"未登录或无权限",icon: "none"});
			var timer = setTimeout(()=>{
				uni.navigateBack({
					delta:1
				})
			},1000);
		}
	},
	//下拉刷新
	onPullDownRefresh(){
		this.loadData('refresh');
	},
	//加载更多
	onReachBottom(){
		this.loadData();
	},
	methods:{
		goDelivery(ordersn){
			uni.navigateTo({
				url:'/pages/shouhou/wuliu?id='+this.refund.id+"&ordersn="+ordersn
			})
		},
		jihui(id){
			uni.navigateTo({
				url:'/pages/shouhou/jihui?id='+id
			})
		},
		quxiaoOrder(id){
			uni.showModal({
			    content: '确定取消申请吗？',
				confirmColor:'#008AB0',
			    success: (e)=>{
			    	if(e.confirm){
						uni.showLoading({
							title: '请稍后'
						})
						uni.request({
							url:uni.getStorageSync('hostname') + '?action=index/order/quxiao_shouhou',
							data:{id:id,token:uni.getStorageSync('cbe_token')},
							method:'POST',
							dataType:'json',
							header: {'content-type' : 'application/x-www-form-urlencoded'},
							success: (resa) => {
								uni.hideLoading();
								var one = resa.data;
								if(one.code==0){
									
								}
							}
						})
			    	}
			    }
			});
		},
		//加载商品 ，带下拉刷新和上滑加载
		async loadData(type='add',loading) {
			//没有更多直接返回
			if(type === 'add'){
				if(this.loadingType === 'nomore'){
					return;
				}
				this.loadingType = 'loading';
			}else{
				this.loadingType = 'more'
			}
			let goodsList = await this.getMyshoucang();
			if(type === 'refresh'){
				this.goodsList = [];
			}
			this.goodsList = this.goodsList.concat(goodsList);
			
			//判断是否还有下一页，有是more  没有是nomore(测试数据判断大于20就没有了)
			this.loadingType  = this.goodsList.length > this.count ? 'nomore' : 'more';
			if(type === 'refresh'){
				if(loading == 1){
					uni.hideLoading()
				}else{
					uni.stopPullDownRefresh();
				}
			}
		},
		async getMyshoucang(){
			return new Promise((resolve, reject) => {
				uni.showLoading({
					title: '请稍后'
				})
				uni.request({
					url:uni.getStorageSync('hostname') + '?action=index/member/myShouhou',
					data: {pn:this.pn,token:uni.getStorageSync('cbe_token')},
					method:'POST',
					dataType:'json',
					header: {'content-type' : 'application/x-www-form-urlencoded'},
					success: (res) => {
						uni.hideLoading()
						if(res.data.code==0){
							this.pn = Number(this.pn)+Number(1);
							this.count = res.data.count;
							resolve(res.data.data);
						}else{
							this.loadingType = "nomore"
						}
					},
					fail: (err) => {
						reject('err')
					}
				});
			})
		},
		//筛选点击
		tabClick(index){
			if(this.filterIndex === index && index !== 2){
				return;
			}
			this.filterIndex = index;
			if(index === 2){
				this.priceOrder = this.priceOrder === 1 ? 2: 1;
			}else{
				this.priceOrder = 0;
			}
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.loadData('refresh', 1);
			uni.showLoading({
				title: '正在加载'
			})
		},
		//详情
		navToDetailPage(item){
			//测试数据没有写id，用title代替
			let id = item.id;
			uni.navigateTo({
				url: `/pages/shouhou/detail?id=${id}`
			})
		},
		stopPrevent(){}
	}
}
